@media only screen and (max-width: 650px) {

  h1{
    font-size: 20px;
    /* background-color: #3ea2c6 */
  }
  .menulist {
    display: none;
  }
  .IconsCon{
    display: none;
    min-width: 500px;
  }
  .menulistMob{
    display: block;
    display: flex;
    justify-content: end;
    align-items: center;
    
  }
}

@media only screen and (min-width: 650px) {
  
  .menulistMob{
    display: none;
  }
  
}
.texthead{
  font-size: 3rem;
}
